import React, { useState, useEffect, useContext } from "react";
import { NavLink } from "react-router-dom";
const Posts = ({ posts, loading }) => {
  return (
    <div className="row">
      {posts.map((post, id) => {
        return (
          <>
            <div className="col-4 mt-4 rounded-sm">
              <img
                src={post.url}
                alt={post.name}
                className="w-100 img-thumbnail"
              />
              <li key={post.id} className="list-group-item w-100 ">
                <NavLink to={`show/${post._id}`}> {post.name} </NavLink>
              </li>
            </div>
          </>
        );
      })}
    </div>
  );
};

export default Posts;
