import React, { useEffect, useState } from 'react'
import CreateIcon from '@mui/icons-material/Create';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import { NavLink, useParams, useHistory } from 'react-router-dom';
import axios from 'axios';


const Details = () => {

    const [getimagedata, setImagedata] = useState([]);
    console.log(getimagedata);

    const { id } = useParams("");
    console.log(id);

    const history = useHistory();


    const getdata = async () => {
        const res = await axios.get(`https://hackerearthchallenge-devendra.herokuapp.com/show/${id}`);
    
        const data = res.data;
        console.log(data);

        if (res.status === 422 || !data) {
            console.log("error ");

        } else {
            setImagedata(data)
            console.log("get data");
        }
    }

    useEffect(() => {
        getdata();
    }, [])

    const deleteimage = async (id) => {

        const res2 = await fetch(`https://hackerearthchallenge-devendra.herokuapp.com/delete/${id}`, {
            method: "DELETE",
            headers: {
                "Content-Type": "application/json"
            }
        });

        const deletedata = await res2.json();
        console.log(deletedata);

        if (res2.status === 422 || !deletedata) {
            console.log("error");
        } else {
            console.log("user deleted");
            history.push("/");
        }

    }

    return (
        <div className="container mt-3">
            <h1 style={{ fontWeight: 400 }}>Image Details</h1>

            <Card sx={{ maxWidth: 800 }}>
                <CardContent>
                    <div className="add_btn mb-3">
                        <NavLink to={`/edit/${getimagedata._id}`}>  <button className="btn btn-primary mx-2"><CreateIcon /></button></NavLink>
                        <button className="btn btn-danger" onClick={() => deleteimage(getimagedata._id)}><DeleteOutlineIcon /></button>
                    </div>
                    <div className="row">
                            <img src={getimagedata.url} className="w-100" alt="profile" />
                        <div className="col">
                            <h3 className="mt-3"><span >{getimagedata.name}</span></h3>
                        
                            <p className="mt-3" style={{fontWeight : 500}}> Description: </p>
                            <span>{getimagedata.detail}</span>
                        </div>
                     
                    </div>

                </CardContent>
            </Card>
        </div>
    )
}

export default Details
